
import ajax from './utils/ajax.js';
export default {
	onLaunch: function() {
		wx.login({
			success:async ({code})=>{
				// console.log('res',code)
				let {data} = await ajax("/getOpenId",{code});
				wx.setStorage({
					key:"openId",
					data:data
				})
			}
		})
	},
	onShow: function() {
		console.log('App Show')
	},
	onHide: function() {
		console.log('App Hide')
	}
}
